/* Animations */
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* General Styles */
body {
  background-image: url('img/bg2.jpg');
  background-size: cover;
  background-position: center;
  margin: 0;
  font-family: 'Lato', sans-serif;
  height: 100vh;
}

/* Playground Container */
.playground-container {
  padding: 70px 20px;
  text-align: center;
  min-height: 100vh;
  box-sizing: border-box;
  animation: fadeUp 1s ease-out forwards;
}

/* About Section */
.about-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  padding: 30px;
  max-width: 850px;
  margin: 0 auto;
  margin-top: 30px;
  text-align: left;
}

.about-image {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.about-text {
  max-width: 700px; 
  text-align: left;
}

.about-text h2 {
  font-family: "Righteous", sans-serif;
  color: #ff7f00;
}

.about-text p {
  font-family: "Lato", sans-serif; 
  color: #fff;
  line-height: 1.6;
}

/* Highlights Section */
.highlights {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 40px 20px;
  text-align: left;
}

.highlight-item {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
}

.highlight-title {
  font-family: 'Righteous', sans-serif;
  color: #ff7f00;
}

.highlight-description {
  font-family: "Lato", sans-serif; 
  color: #fff;
  line-height: 1.6;
}

/* Hobbies Section */
.heading-heading {
  font-family: "Righteous", sans-serif;
  color: #000000;
}

.hobbies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
}

.hobby-card {
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
}

.hobby-image {
  width: 100%;
  height: 120px;
  object-fit: cover;
}

.hobby-name {
  font-family: 'Righteous', sans-serif;
  color: #ff7f00;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .about-text {
    max-width: 100%;
  }

  .highlights {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }

  .hobbies-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

@media (max-width: 480px) {
  .playground-container {
    padding: 50px 15px;
  }

  .about-section {
    padding: 20px;
  }

  .highlights {
    grid-template-columns: 1fr;
  }

  .hobbies-grid {
    grid-template-columns: 1fr;
  }

  .about-image {
    width: 100px;
    height: 100px;
  }

  .hobby-image {
    height: 100px;
  }
}
